<template>
  <view>
    <view class="bg-img"
          :style="{backgroundImage:`url(${songInfo.al.picUrl})`}" />
      <!-- 头部 标题栏-->
      <view class="head">
        <!-- 返回键 -->
        <view class="back-icon">
          <u-icon name="arrow-left" size="50"/>
        </view>
        <!-- 歌名和歌手 -->
        <view class="song-songer">
          <!-- 歌名 -->
          <view class="f-center">
            {{songInfo.al.name}}
          </view>
          <!-- 歌手 -->
          <view class="f-center songer">
            {{songInfo.ar[0].name}}
          </view>
        </view>
        <!-- 分享 -->
        <view class="back-icon">
          <u-icon name="share" size="50"/>
        </view>
      </view>
      <!-- 歌词海报 -->
      <view class="content">

      </view>
      <!-- 控制栏 -->
      <view class="control">

      </view>
<!--      <audio :src="songUrl"
             autoplay
             controls></audio> -->
  </view>
</template>

<script>
import { recommend } from "@/api/index/recommend.js";
let api = new recommend();
export default {
  data () {
    return {
      songUrl: '',
      songInfo: ''
    };
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      this.getSongUrl()
      this.getMusicDetail()
	  this.getTrack()
    },
    getSongUrl () {
      api.getSongUrl(this.$store.state.songsListIds[0]).then(res => {
        this.songUrl = res.data[0].url
        console.log(res.data[0])
      })
    },

    getMusicDetail () {
      api.getMusicDetail(this.$store.state.songsListIds[0]).then(res => {
        // this.songInfo = res.songs[0].al; 
        // console.log(res.songs[0].al)
        this.songInfo = res.songs[0]
		console.log(res)
      })
    },
    getMusicLyric () {
      api.getMusicLyric(this.musicId).then(res => {
        // this.lyric = res.lrc.lyric        
      })
    }

  }
}	
</script>

<style lang="scss">
.bg-img {
  position: fixed;
  left: 0;
  right: 0;
  top: -50px;
  height: 100%;
  background-color: #161824;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: auto 100%;
  transform-origin: center top;
  transform: scale(1.5);
  transition: opacity 0.3s linear;
  z-index: -100;
  filter: blur(16px) brightness(50%);
}
.head {
	position: fixed;
	width: 750rpx;
	margin-top: 20rpx;
	padding: 0 10rpx 0 10rpx;
	top: 0;
	color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .songer{
	  font-size: 20rpx;
	  height: 50rpx;
  }
  
}
</style>
